import React from 'react';

import localStorageHelper from 'helpers/localStorageHelper';
import orderService from 'services/orderService';
import Icon from 'components/common/Icon/icon';

import preloader from 'styles/icons/preloader.svg';
import './ordersWithStausesSection.scss';

export default class OrdersWithStatusesSection extends React.PureComponent {
    constructor() {
        super();
        this.state = {
            orders: [],
            isLoading: true
        };
    }

    componentDidMount = async () => {
        const result = await orderService.setByUserId(localStorageHelper.getId());
        this.setState({
            orders: result,
            isLoading: false
        });
    }

    renderOrders = () => {

    }

    render() {
        if (this.state.isLoading) {
            return (
                <div className="user-orders-section__preloader">
                    <Icon iconClassName="user-orders-section__preloader-icon" icon={preloader} />
                </div>
            );
        }

        return (
            <section className="orders-with-statuses-section">
                <h1 className="orders-with-statuses-section__title">My orders</h1>
                <table>
                    {this.renderOrders()}
                </table>
            </section>
        );
    }
}
